@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;700&display=swap);
* {
  padding: 0;
  margin: 0;
}
body {
  font-family: "Poppins", sans-serif;
  background-color: #f0f9ff;
  color: #41a5ee;
}

img {
  width: 30vw;
}

/* total number section css start here */
.landing-title {
  text-align: center;
  background-color: #276678;
  padding: 2.5rem 2.5rem 1rem 2.5rem;
  text-transform: uppercase;
  font-size: 4rem;
  color: #f0f9ff;
  text-shadow: 2px 2px 2px #85daf9;
  margin-bottom: 1px;
}
.landing-container {
  color: #85DAF9;
  display: grid;
  grid-template-columns: 60% 40%;
  -webkit-align-items: center;
          align-items: center;
  justify-items: center;
  padding-top: 2rem;
  background-color: #276678;
}
.container-totalNum {
  width: 90%;
  margin: auto;
  display: grid;
  padding: 1rem;
  grid-template-columns: repeat(2, 50%);
}
.board {
  box-shadow: rgba(255, 255, 255, 0.5) 0px 2px 8px 0px;
  border-radius: 5px;
  margin: .5rem;
  padding: 1rem;
  height: auto;
  display: grid;
  -webkit-align-content: space-around;
          align-content: space-around;
}
.overall {
  grid-column: 1/3;
  text-align: center;
}
.count-up {
  font-size: 3rem;
  color: #f0f9ff;
}

/* select css start here */
select {
  border: none;
  border-bottom: 3px solid #276678;
  color: #276678;
  background: transparent;
  padding: 0.5rem 1rem;
  font-family: "Poppins", sans-serif;
  font-size: 1rem;
}
select:focus {
  outline: none;
}
.select-container {
  width: 40%;
  margin: auto;
  text-align: center;
}

/* charts css start here */
.default-chart,
.age-chart-container {
  padding: 4rem 0;
  width: 80%;
  margin: auto;
}
.doughnut-chart {
  padding: 2rem;
}
.doughnut-chart-container {
  padding: 4rem 0;

  display: grid;
  grid-template-columns: repeat(2, 50%);
}
.gender-chart-container {
  padding: 4rem 0;
  width: 50%;
  margin: auto;
}

@media (max-width: 768px){
.board h3{
    font-size: 1rem;
}
.count-up {
  font-size: 1rem;
}  
}

@media (max-width: 426px){
    .landing-title{
        font-size: 1.5rem;
    }
    img{
        grid-row: 1/2;
        width: 60%;
    }
    .landing-container {
        grid-template-columns: 100%;
      }
      .default-chart,
.age-chart-container {
  padding: 2rem 0;
  /* width: 95%;
  margin: auto; */
}
.doughnut-chart {
    padding: 0;
  }
  .doughnut-chart-container {
    padding: 2rem 0;
  
    display: grid;
    grid-template-columns: 100%;
    justify-items: center;
  }
  canvas.chartjs-render-monitor{
      height: 95%;
      width: 95%;
      margin: auto;
  }
  .gender-chart-container{
      width: 100%;
      padding: 2rem 0;
  }
}
